/* eslint-disable no-alert */
import fetch from 'node-fetch';

const getStringCoordinate = (coord) => {
  let coordinate = coord.toFixed(2);
  let sign = true;
  if (coordinate < 0) {
    coordinate = -coordinate;
    sign = false;
  }
  let degree = Math.floor(coordinate);
  let second = Math.round((coordinate - degree) * 100);
  if (degree < 10) degree = (`0${degree}`).slice(-2);
  if (second < 10) second = (`0${second}`).slice(-2);
  if (!sign) {
    degree = `-${degree}`;
  }
  return `${degree}° ${second}'`;
};

const load = item => localStorage.getItem(item);
const save = (item, value) => localStorage.setItem(item, value);

const getForecast = async (lat, long, language, temp) => {
  const temperature = (temp === 'C') ? 'si' : 'us';
  const proxyUrl = 'https://cors-anywhere.herokuapp.com/';
  const url = `https://api.darksky.net/forecast/add2f8d4f1f8122738e92d4281585b49/${lat},${long}?lang=${language}&units=${temperature}`;
  const response = await fetch(proxyUrl + url);
  const res = await response.json();
  return res;
};

const setCurrentLocation = async (names) => {
  const url = 'https://ipinfo.io/json?token=43e8f2eac66d3c';
  const response = await fetch(url);
  const res = await response.json();
  return `${res.city}, ${names[res.country]}`;
};

const setDay = (d) => {
  if (d === 6) {
    return 0;
  }
  return d + 1;
};

const getCoordinateFromName = async (key, language) => {
  const url = `https://api.opencagedata.com/geocode/v1/json?q=${key}&key=55a82ea93acb4e678fc4e54d4aa7299b&language=${language}&pretty=1&no_annotations=1`;
  const response = await fetch(url);
  const res = await response.json();
  return res;
};

const getImageLink = async (key) => {
  const url = `https://api.unsplash.com/photos/random?query=${key}&client_id=12576695bbf5567d1e8e843a13596f3a9c1eff04fb3ea41c59440fed64ec5eda`;
  const image = new Image();
  image.crossOrigin = 'Anonymous';
  const response = await fetch(url);
  const res = await response.json();
  image.src = await res.urls.small;
  return image;
};

const celsiusToFahrenheit = c => Math.round(c * 9 / 5 + 32);
const fahrenheitToCelsius = f => Math.round((f - 32) * 5 / 9);

const changeTemperature = (v, temp) => {
  const temperature = +v.innerHTML;
  switch (temp) {
    case 'F': return celsiusToFahrenheit(temperature);
    case 'C': return fahrenheitToCelsius(temperature);
    default: return undefined;
  }
};

const checkData = (data) => {
  if (!data) {
    alert('Wrong data! Try again!');
    return false;
  }
  return true;
};

const checkInput = (data) => {
  if (!data) {
    alert('Nothing to search! Try again!');
    return false;
  }
  return true;
};

export {
  load, save, setCurrentLocation, getStringCoordinate, getForecast,
  setDay, getCoordinateFromName, getImageLink, changeTemperature, celsiusToFahrenheit,
  fahrenheitToCelsius, checkData, checkInput,
};
