import '../styles/desktop.scss';
import '../styles/laptop.scss';
import '../styles/mobile.scss';
import en from '../languages/en';
import ru from '../languages/ru';
import be from '../languages/be';
import { load } from '../resourses/utility';

export default class AppView {
  constructor() {
    this.language = load('language') || 'En';
    switch (this.language) {
      case 'Ru': Object.assign(this, ru);
        break;
      case 'Be': Object.assign(this, be);
        break;
      default: Object.assign(this, en);
        break;
    }
    this.temperature = load('temperature') || 'C';
  }

  render() {
    document.body.innerHTML = `
    <div class = 'container' id = 'container'>
      <form class = 'control-block' id = 'control-block'>
        <button class = 'update-button' id = 'update-button' type = 'button'></button>
        <select class = 'language-select' id = 'language-select'>
          <option id = 'En'>En</option>
          <option id = 'Ru'>Ru</option>
          <option id = 'Be'>Be</option>
        </select>
        <select class = 'temperature-select' id = 'temperature-select'>
          <option id = 'C'>C</option>
          <option id = 'F'>F</option>
        </select>
      </form>
      <form class = 'search-block' id = 'search-block' onsubmit="return false;">
        <input type = 'text' class = 'input' id = 'input' placeholder = ${this.search}>
        <button class = 'voice-input' id = 'voice-input' type = 'button'></button>
        <button class = 'search-button' id = 'search-button' type = 'button'>${this.search}</button>
      </form>
      <h1 class = 'location' id = 'location'></h1>
      <h2 class = 'date' id = 'date' ></h2>
      <div class = 'today' id = 'today'>
      </div>
      <div class = 'forecast-block' id = 'forecast-block'>
      </div>
      <div class = 'map' id = 'map'></div>
      <h3 class = 'coordinate' id = 'coordinate'>
      </h3>
    </div>`;
    this.container = document.querySelector('#container');
    document.querySelector(`#${this.language}`).setAttribute('selected', '');
    document.querySelector(`#${this.temperature}`).setAttribute('selected', '');
  }
}
