import { getStringCoordinate, setDay } from '../resourses/utility';

const setCoordinateView = (appView, lat, long) => {
  const coordinate = document.querySelector('#coordinate');
  coordinate.innerHTML = `${appView.latitude}: ${getStringCoordinate(lat)}<br>
  ${appView.longitude}: ${getStringCoordinate(long)}`;
};

const dateNowView = async (appView, dateField, timezone) => {
  const appV = appView;
  const setTime = () => {
    const now = new Date();
    const options = {
      weekday: 'long', year: 'numeric', month: 'long', day: 'numeric', timeZone: `${timezone}`, hour12: false,
    };
    const dF = dateField;
    appV.today = now.getDay();
    const localeData = now.toLocaleTimeString('en-GB', options).split(',');
    const date = localeData[1].split(' ');
    const dateNow = date[1];
    const year = date[3];
    const time = localeData[2];
    const day = appView.weekShort[appV.today];
    const month = appView.month[now.getMonth()];
    appV.currentMonth = month;
    dF.innerHTML = `${day}, ${dateNow}, ${month} ${year}, ${time}`;
  };
  setTime();
  appV.timer = setInterval(setTime, 1000);
};

const setForecastView = (forecast, appView) => {
  let day = appView.today;
  const forecastBlock = document.querySelector('#forecast-block');
  forecastBlock.innerHTML = `<div class = 'forecast tomorrow' id ='tomorrow'>
                              <h2><span>${appView.week[day = setDay(day)]}</span></h2>
                              <p><span id = 'temperature-tomorrow'>${Math.round(forecast.daily.data[0].temperatureLow)}</span>°</p>
                              <div><img src = './assets/${forecast.daily.data[0].icon}.gif'></div>
                            </div>
                            <div class = 'forecast day-after-tomorrow' id ='day-after-tomorrow'>
                              <h2><span>${appView.week[day = setDay(day)]}</span></h2>
                              <p><span id = 'temperature-day-after-tomorrow'>${Math.round(forecast.daily.data[1].temperatureLow)}</span>°</p>
                              <div><img src = './assets/${forecast.daily.data[1].icon}.gif'></div>
                            </div>
                            <div class = 'forecast day-after-day-after-tomorrow' id ='day-after-day-after-tomorrow'>
                              <h2><span>${appView.week[day = setDay(day)]}</span></h2>
                              <p><span id = 'temperature-day-after-day-after-tomorrow'>${Math.round(forecast.daily.data[2].temperatureLow)}</span>°</p>
                              <div><img src = './assets/${forecast.daily.data[2].icon}.gif'></div>
                            </div>`;
};

const setTodayForecastView = (forecast, appView) => {
  const today = document.querySelector('#today');
  today.innerHTML = `<p class = 'temperature-now'><span id = 'temperature-now'>${Math.round(forecast.currently.temperature)}</span>°</p>
                     <div class = 'weather-now' id = 'weather-now'><img src = './assets/${forecast.currently.icon}.gif'></div>
                     <h2 class = 'review' id = 'review'>
                        <span id = 'summary'>${forecast.currently.summary}</span><br>
                        <span>${appView.feelsLike}: ${forecast.currently.apparentTemperature}</span>°<br>
                        <span>${appView.wind}: ${forecast.currently.windSpeed}</span> ${appView.ms}<br>
                        <span>${appView.humidity}: ${Math.round(forecast.currently.humidity * 100)}</span>%
                     </h2>`;
};

export {
  setCoordinateView, dateNowView, setForecastView, setTodayForecastView,
};
