/* eslint-disable no-alert */
import AppView from './views/appView';
import {
  save, setCurrentLocation, getCoordinateFromName, load,
  changeTemperature, checkData, checkInput,
} from './resourses/utility';
import { setLocaleData, setSearchedData } from './resourses/set-data';
import names from './languages/names';

const appView = new AppView();
appView.render();

const location = document.querySelector('#location');

if (load('cityData')) {
  setSearchedData(JSON.parse(load('cityData')), appView, location);
} else {
  setCurrentLocation(names).then(city => setLocaleData(appView, city, location))
    .then(city => getCoordinateFromName(city, appView.language))
    .then(cityData => save('cityData', JSON.stringify(cityData)));
}

const languageSelect = document.querySelector('#language-select');
languageSelect.addEventListener('change', () => {
  save('language', languageSelect.value);
  window.location.reload();
});

const temperatureSelect = document.querySelector('#temperature-select');
temperatureSelect.addEventListener('change', () => {
  save('temperature', temperatureSelect.value);
  appView.temperature = temperatureSelect.value;
  const temperatureNow = document.querySelector('#temperature-now');
  const temperatureTomorrow = document.querySelector('#temperature-tomorrow');
  const temperatureDayAfterTomorrow = document.querySelector('#temperature-day-after-tomorrow');
  const tempDayAfterDayAfterTomorrow = document.querySelector('#temperature-day-after-day-after-tomorrow');
  temperatureNow.innerHTML = changeTemperature(temperatureNow, temperatureSelect.value);
  temperatureTomorrow.innerHTML = changeTemperature(temperatureTomorrow, temperatureSelect.value);
  temperatureDayAfterTomorrow.innerHTML = changeTemperature(temperatureDayAfterTomorrow,
    temperatureSelect.value);
  tempDayAfterDayAfterTomorrow.innerHTML = changeTemperature(tempDayAfterDayAfterTomorrow,
    temperatureSelect.value);
});

const updateButton = document.querySelector('#update-button');
updateButton.addEventListener('click', () => {
  setSearchedData(JSON.parse(load('cityData')), appView, location);
});

const inputField = document.querySelector('#input');
inputField.addEventListener('keyup', async (e) => {
  if (e.key === 'Enter') {
    if (!checkInput(inputField.value)) return;
    const cityData = await getCoordinateFromName(inputField.value, appView.language);
    if (!checkData(cityData.results.length)) return;
    setSearchedData(cityData, appView, location);
    save('cityData', JSON.stringify(cityData));
  }
});

const searchButton = document.querySelector('#search-button');
searchButton.addEventListener('click', async () => {
  if (!checkInput(inputField.value)) return;
  const cityData = await getCoordinateFromName(inputField.value, appView.language);
  if (!checkData(cityData.results.length)) return;
  setSearchedData(cityData, appView, location);
  save('cityData', JSON.stringify(cityData));
});

const recognizer = new (window.SpeechRecognition || window.webkitSpeechRecognition
  || window.mozSpeechRecognition || window.msSpeechRecognition)();
recognizer.continuous = true;
recognizer.lang = appView.language;
recognizer.onresult = async (event) => {
  inputField.value = event.results[0][0].transcript;
  const cityData = await getCoordinateFromName(event.results[0][0].transcript, appView.language);
  if (!checkData(cityData.results.length)) return;
  setSearchedData(cityData, appView, location);
  save('cityData', JSON.stringify(cityData));
};

const voiceInput = document.querySelector('#voice-input');
voiceInput.addEventListener('mousedown', () => {
  recognizer.start();
});

voiceInput.addEventListener('mouseup', () => {
  recognizer.stop();
});
