import { getForecast, getImageLink, setCurrentLocation } from './utility';
import {
  setCoordinateView, dateNowView, setForecastView, setTodayForecastView,
} from '../views/views';
import map from './map';
import names from '../languages/names';

const setData = async (latitude, longitude, appView, city) => {
  map(longitude, latitude);
  setCoordinateView(appView, latitude, longitude);
  const forecast = await getForecast(latitude, longitude,
    appView.language, appView.temperature);
  setTodayForecastView(forecast, appView);
  const dateField = document.querySelector('#date');
  dateNowView(appView, dateField, forecast.timezone);
  setForecastView(forecast, appView);
  const appV = appView;
  const image = await getImageLink(`${city}+${appView.currentMonth}+weather`);
  image.onload = () => {
    appV.container.style.backgroundImage = `url(${image.src})`;
    appV.container.style.backgroundSize = 'cover';
  };
};

const setLocaleData = async (appView, city, location) => {
  const options = {
    enableHighAccuracy: true,
    timeout: 5000,
    maximumAge: 0,
  };

  async function success(pos) {
    const loc = location;
    loc.innerHTML = await setCurrentLocation(names);
    const crd = await pos.coords;
    setData(crd.latitude, crd.longitude, appView, city);
  }

  function error(err) {
    // eslint-disable-next-line no-alert
    alert(`ERROR(${err.code}): ${err.message}`);
  }

  navigator.geolocation.getCurrentPosition(success, error, options);
  return city;
};

const setSearchedData = (cityData, appView, location) => {
  if (!cityData) {
    localStorage.removeItem('cityData');
    window.location.reload();
    return;
  }
  const loc = location;
  if (!cityData.results.length) {
    loc.innerHTML = 'Location undefined';
    return;
  }
  const dataArray = cityData.results[0].formatted.split(',');
  loc.innerHTML = `${`${dataArray[0]}. ${dataArray[1] || ''}`}`;
  const latitude = cityData.results[0].geometry.lat;
  const longitude = cityData.results[0].geometry.lng;
  clearInterval(appView.timer);
  setData(latitude, longitude, appView, cityData.results[0].formatted);
};

export { setLocaleData, setSearchedData };
